import { getDateWithYeadString } from "../../utils/utils";

export interface IPhysicalData {
    steps: number;
    pulse: number;
    weight: number;
    fatPercents: number;
};

export enum Intencity {
    low = "",
    middle = "",
    height = "",
};
export enum Mood {
    low = "",
    middle = "",
    height = "",
}

export interface IChallenge {
    goal: string; //   
    activity: string; //  ,  
    trainingDurationMinutes: number; //  , 
    trainingIntencity: Intencity;
    afterTraining: string; //   
    nutrition: string; //  /  /  /  / : 
};
export interface IWellBeing {
    mood: Mood;
    reason: string; //     
}
export interface IProgress {
    succeed: boolean; //  ?
    successToday: string; //  
    successTomorrow: string; //  
};
export interface IMotivation {
    quoteOfTheDay: string; //  
    whySportIsImportant: string;
};
export interface IAchievementData {
    challengeData: IChallenge;
    physicalData: IPhysicalData;
    wellBeing: IWellBeing; // 
    progress: IProgress;
    motivation: IMotivation;
    date: Date;
};
const AchievementCard = (props: {data: IAchievementData}) => {
    const {data} = props;
    return <div className="avievement-card">
        <div className="avievement-card-date-header">
            <h2>{getDateWithYeadString(data.date)}</h2>
        </div>
        <div className="avievement-card-block">
            <p><b>   :</b> {data.challengeData.goal}</p>
            <p><b> ,  :</b> {data.challengeData.activity}</p>
            <p><b>  ():</b> {data.challengeData.trainingDurationMinutes}</p>
            <p><b> :</b> {data.challengeData.trainingIntencity}</p>
            <p><b>  :</b> {data.challengeData.afterTraining}</p>
            <p><b>, :</b> {data.challengeData.nutrition}</p>
        </div>
        <div className="avievement-card-block">
            <p><b>:</b> {data.physicalData.pulse}</p>
            <p><b> :</b> {data.physicalData.steps}</p>
            <p><b> :</b> {data.physicalData.weight}</p>
            <p><b>% :</b> {data.physicalData.fatPercents}</p>
        </div>
        <div className="avievement-card-block">
            <p><b>:</b> {data.wellBeing.mood}</p>
            <p><b>    :</b> {data.wellBeing.reason}</p>
        </div>
        <div className="avievement-card-block">
            <p><b> ?:</b> {data.progress.succeed ? "" : ""}</p>
            <p><b> :</b> {data.progress.successToday}</p>
            <p><b>  :</b> {data.progress.successTomorrow}</p>
        </div>
        <div className="avievement-card-block">
            <p><b> :</b> {data.motivation.quoteOfTheDay}</p>
            <p><b>  :</b> {data.motivation.whySportIsImportant}</p>
        </div>
    </div>
};
const AchievementsPage = (props: {data: Array<IAchievementData>}) => {
    const {data} = props;
    return <>


    {
        data.map(x => <AchievementCard data={x}/>)
    }
    </>
}

export {AchievementsPage, AchievementCard}
